<script>
export let message = "";
export let has_sender = false;

let processed = false;
let messagesArray = message.split(/\s+/);

let messagesArrayProcessed = [];

function detectLinks(text) {
    const urlRegex = /(https?:\/\/[^\s]+)/g;

    if(urlRegex.test(text)){
        messagesArrayProcessed.push({
            text: text,
            is_link: true
        })
    }
    else{
        messagesArrayProcessed.push({
            text: text,
            is_link: false
        })
    }
}

for (let msg of messagesArray) {
    detectLinks(msg);
    if (messagesArray[messagesArray.length - 1] === msg) {
        processed = true;
    }
}

</script>

{#if processed}
    {#each messagesArrayProcessed as item}
        {#if item.is_link}
            <a href={item.text} rel="noreferrer" class="target_links {has_sender ? "has-text-white" : "has-text-primary"}" target="__blank">{item.text}</a>
        {:else}
            {item.text}
        {/if}
        &nbsp;
    {/each}
{/if}

