import { turpial } from 'tripod-styles/src/libraries/turpial';
import { ShadowRoot } from '../libraries/ShadowRoot';
import App from '../LiveChatWidget/LiveChatWidget.svelte';

window.turpial = turpial;
let app = document.querySelector(".livechat-3pod");
let mounted = turpial.mount(app, `<livechat-module class="svelte-app-child index-module"></livechat-module>`);
// only for PRODUCTION uncomment this section.
if(window.location.host !== "localhost:5000"){
    app = mounted.attachShadow({mode: 'open'});
}

let application = new App({
    target: app,
});
ShadowRoot.update(app.querySelector(".svelte-module-chat"));

if(window.location.host !== "localhost:5000"){
    setTimeout(() => {
        let styles = document.querySelector("#injected-css-sv");
        app.appendChild(styles);
    }, 150);
}


