
import { writable } from 'svelte/store';

/**
 * Store to track the session status of live chat rooms.
 * Prevents multiple initializations (fetching history, sending init messages)
 * when toggling the chat bubble.
 */
function createLiveChatSession() {
    const { subscribe, update } = writable({
        rooms: {} // { [roomId]: { isInitiated: boolean, messages: [], participants: { sender: {}, receiver: {} }, unreadCount: number } }
    });

    return {
        subscribe,
        /**
         * Mark a room as initiated and optionally set its messages.
         * @param {string} roomId 
         */
        setInitiated: (roomId) => update(state => {
            if (!state.rooms[roomId]) {
                state.rooms[roomId] = { isInitiated: true, messages: [], participants: { sender: {}, receiver: {} }, unreadCount: 0 };
            } else {
                state.rooms[roomId].isInitiated = true;
            }
            return state;
        }),
        /**
         * Set participant data for a room.
         * @param {string} roomId 
         * @param {string} role 'sender' | 'receiver'
         * @param {object} data 
         */
        setParticipant: (roomId, role, data) => update(state => {
            if (!state.rooms[roomId]) {
                state.rooms[roomId] = { isInitiated: false, messages: [], participants: { sender: {}, receiver: {} }, unreadCount: 0 };
            }
            state.rooms[roomId].participants[role] = data;
            return state;
        }),
        /**
         * Update messages for a specific room.
         * @param {string} roomId 
         * @param {array} messages 
         */
        setMessages: (roomId, messages) => update(state => {
            if (!state.rooms[roomId]) {
                state.rooms[roomId] = { isInitiated: false, messages: [], participants: { sender: {}, receiver: {} }, unreadCount: 0 };
            }
            state.rooms[roomId].messages = messages;
            return state;
        }),
        /**
         * Add a single message to a room.
         * @param {string} roomId 
         * @param {object} message 
         * @param {boolean} isUnread 
         */
        addMessage: (roomId, message, isUnread = false) => update(state => {
            if (!state.rooms[roomId]) {
                state.rooms[roomId] = { isInitiated: false, messages: [], participants: { sender: {}, receiver: {} }, unreadCount: 0 };
            }
            
            // Prevent duplicates
            const exists = state.rooms[roomId].messages.some(m => m.id === message.id);
            if (!exists) {
                state.rooms[roomId].messages = [...state.rooms[roomId].messages, message];
                if (isUnread) {
                    state.rooms[roomId].unreadCount = (state.rooms[roomId].unreadCount || 0) + 1;
                }
            }
            
            return state;
        }),
        /**
         * Reset unread count for a room.
         * @param {string} roomId 
         */
        resetUnreadCount: (roomId) => update(state => {
            if (state.rooms[roomId]) {
                state.rooms[roomId].unreadCount = 0;
            }
            return state;
        }),
        /**
         * Check initialization status.
         */
        isInitiated: (roomId) => {
            let initiated = false;
            subscribe(state => {
                initiated = !!state.rooms[roomId]?.isInitiated;
            })();
            return initiated;
        }
    };
}

export const LiveChatSession = createLiveChatSession();
