/**
 * Search for a extension item.
 * @param {*} extension 
 * @param {*} version 
 * @returns 
 */
function FileExtensionIcon(extension, version){
    version = version || "default";
    if(version === "default"){
        return DesignRequestFilesExtensions.find((ext)=> ext.name === extension );
    }

    if(version === "small"){
        return DesignRequestFilesExtensionsSmall.find((ext)=> ext.name === extension );
    }
}


/**
 * Set separated extensions to replace with icon version image.
 * @param {string} url 
 * @param {string} extensions 
 */
function SetUrlFileImage(url, extensions, version, has_user = true){
    version = version || "default";
    extensions = extensions || "";
    extensions = extensions.split("|");

    let current_extension = url.split("/").reverse()[0].split(".").reverse()[0];
    extensions.map((ext)=>{
        if(ext === current_extension){
            if (has_user) {
                url = FileExtensionIcon(ext, version).img;
            }else{
                url = FileExtensionIcon(ext, version).white;
            }
        }
    })

    return url;
}

