<script>
import { onMount, onDestroy, tick } from 'svelte';
import MessagesParser from '../ChatTotal/MessagesParser.svelte';
import FilesChat from '../ChatTotal/FilesChat.svelte';
import InternationalNumberInput from '../../Inputs/InternationalNumberInput.svelte';
import {turpial} from '../../../libraries/turpial';
import { toast_pop } from '../../../libraries/toast';
import { ApplicationData } from '../../../libraries/ApplicationData';
import { LiveChatSession } from '../../../libraries/LiveChatSession';
import { FormatDate } from '../../../libraries/FormatDate';



// Use relative path for now, assuming these components export what we need
// If they rely on stores that might not be present in the consumer app, we might need to mock them or ask user.
// However, user said "Based on this component... Check the functionalities that it have".
// I will assume standard Svelte functionality for now.

export let roomId;
export let workerUrl; // Default local worker
export let containerStyles = "";
export let containerClasses = "";
export let type_of_user = "guest"; // guest, agent
export let participantInfo = null;
export let visible = true;

if(!window.IS_ONLINE){
    workerUrl = 'http://127.0.0.1:8787'; // Default local worker
}

if(window.IS_ONLINE){
    workerUrl = 'https://livechat-socket.3pod.io'; 
}


const role_map = {
    'guest': 'guest',
    'user': 'user',
    'admin': 'agent',
    'superuser': 'agent',
    'agent': 'agent'
};

$: user_role = role_map[type_of_user] || 'guest';

// State
let step = 'register'; // 'register' | 'chat'
let participant = {
    name: '',
    email: '',
    phone: '',
    id_participant: ''
};

$: messages = $LiveChatSession.rooms[roomId]?.messages || [];
$: receiverParticipant = participantInfo || $LiveChatSession.rooms[roomId]?.participants?.receiver || null;

$: if (visible && roomId) {
    const unreadCount = $LiveChatSession.rooms[roomId]?.unreadCount || 0;
    
    if (unreadCount > 0) {
        if (participant.id_participant) {
            fetch(`${workerUrl}/read/${roomId}`, {
                method: 'POST',
                headers: { 'Content-Type': 'application/json' },
                body: JSON.stringify({ id_participant: participant.id_participant })
            }).catch(err => console.error('Error marking as read:', err));
        }
        LiveChatSession.resetUnreadCount(roomId);
    }
    
    if (messages.length > 0) {
        scrollToBottom();
    }
}
let newMessage = '';
let socket;
let status = 'connecting'; // connecting, connected, error
let chat_container;
let registered_participant = false;

let validations_phones = {
    phone: false
};

// valid types of users: guest, user, admin, superuser
if(type_of_user !== "guest" && type_of_user !== "user" && type_of_user !== "admin" && type_of_user !== "superuser"){
    type_of_user = "guest";
}

if($ApplicationData.user){
    type_of_user = "user";
    registered_participant = true;
    participant = {
        name: $ApplicationData.user.name,
        email: $ApplicationData.user.email,
        phone: `${$ApplicationData.user.dial}${$ApplicationData.user.phone}`,
        id_participant: $ApplicationData.user.public_key
    }
}

if($ApplicationData.admin){
    type_of_user = "agent";
    registered_participant = true;
    participant = {
        name: $ApplicationData.admin.name,
        email: $ApplicationData.admin.email,
        phone: "-",
        id_participant: $ApplicationData.admin.public_key
    }
}

if($ApplicationData.superuser){
    type_of_user = "agent"
    registered_participant = true;
    participant = {
        name: $ApplicationData.superuser.name,
        email: $ApplicationData.superuser.email,
        phone: "-",
        id_participant: $ApplicationData.superuser.public_key
    }
}



function getDeviceInfo() {
    const ua = navigator.userAgent;
    let browser = "Unknown";
    let os = "Unknown";
    let device = "Desktop";

    if (ua.includes("Firefox")) browser = "Firefox";
    else if (ua.includes("Chrome")) browser = "Chrome";
    else if (ua.includes("Safari")) browser = "Safari";
    else if (ua.includes("Edge")) browser = "Edge";

    if (ua.includes("Windows")) os = "Windows";
    else if (ua.includes("Mac")) os = "MacOS";
    else if (ua.includes("Linux")) os = "Linux";
    else if (ua.includes("Android")) os = "Android";
    else if (ua.includes("iPhone")) os = "iOS";

    if (/Mobi|Android/i.test(ua)) device = "Mobile";

    return { ua, browser, os, device, referrer: document.referrer || 'Direct' };
}

$: role = (user_role === 'agent') ? 'agent' : 'user';

function joinChat() {
    if (!participant.name || !participant.email) {
        toast_pop("¡Error!", "Por favor agrega tu nombre y correo.", "error");
        return;
    }

    if(!participant.phone || !validations_phones.phone){
        toast_pop("¡Error!", "Por favor agrega tu número de teléfono válido.", "error");
        return;
    }
    
    if (!participant.id_participant) {
        participant.id_participant = 'guest_' + turpial.random_string(32);
        // set the room with the same 
        roomId = participant.id_participant;
    }

    // Add device info
    participant = { ...participant, ...getDeviceInfo() };

    // Save to localStorage
    localStorage.setItem('minichat_participant', JSON.stringify(participant));

    step = 'chat';
    LiveChatSession.setParticipant(roomId, 'sender', participant); // Sync sender
    getHistory();
    connect();
}

function connect() {
    status = 'connecting';
    const protocol = window.location.protocol === 'https:' ? 'wss:' : 'ws:';
    // Adjust logic if workerUrl is full URL
    let wsUrl;
    if (workerUrl.startsWith('http')) {
          wsUrl = workerUrl.replace(/^http/, 'ws') + `/chat/${roomId}`;
    } else {
          wsUrl = `${protocol}//${window.location.host}/chat/${roomId}`;
    }

    socket = new WebSocket(wsUrl);

    socket.onopen = () => {
        status = 'connected';
        console.log('Connected to chat room:', roomId);
        
        // Send init
        if (!LiveChatSession.isInitiated(roomId)) {
            socket.send(JSON.stringify({
                type: 'init',
                ...participant,
                user_role: user_role,
                id_room: roomId,
                role: role
            }));
            LiveChatSession.setInitiated(roomId);
        }
    };

    socket.onmessage = (event) => {
        
        try {
            const data = JSON.parse(event.data);
            if (data.type === 'message') {
                // Adapt message structure to ChatTotal's expected format if needed
                // ChatTotal expects: { type_sender: 'user' | 'admin' | 'system', msg: string, created_at: date, files: [] }
                // Incoming data: { type: 'message', content: string, id_participant: string, timestamp: string }
                
                const isMe = data.is_me;
                const mappedMsg = {
                    id: data.id || Date.now() + Math.random(), 
                    type_sender: isMe ? 'user' : 'agent', 
                    msg: data.content,
                    created_at: data.created_at || data.timestamp,
                    files: data.files || [], 
                    metadata: data.metadata || {}
                };

                LiveChatSession.addMessage(roomId, mappedMsg, !visible && !isMe);
                // Also update participant info from broadcast if it's the other person
                if (!isMe && data.participant) {
                    LiveChatSession.setParticipant(roomId, 'receiver', data.participant);
                }
                scrollToBottom();
            } else if (data.type === 'history') {
                const history = data.messages.map(m => ({
                    id: m.id,
                    type_sender: m.is_me ? 'user' : 'agent',
                    msg: m.content,
                    created_at: m.created_at,
                    files: m.files || [],
                    metadata: m.metadata || {}
                }));
                LiveChatSession.setMessages(roomId, history);
                scrollToBottom();
            }
        } catch (e) {
            console.error('Failed to parse message:', e);
        }
    };

    socket.onerror = () => {
        status = 'error';
    };

    socket.onclose = () => {
        if (status !== 'error') status = 'connecting';
        // Simple reconnect
        setTimeout(() => {
            if (step === 'chat') connect();
        }, 3000);
    };
}

function sendMessage() {
    console.log("Attempting to send message. Status:", status, "Message:", newMessage);
    if (!newMessage.trim() || status !== 'connected') {
        console.warn("Cannot send message: socket not connected or message empty.");
        return;
    }

    const msg = {
        type: 'message',
        text: newMessage,
        user_role: user_role,
        id_room: roomId,
        role: role
    };
    
    socket.send(JSON.stringify(msg));
    
    newMessage = '';
    scrollToBottom();
}

async function getHistory() {
    if (LiveChatSession.isInitiated(roomId)) {
        console.log("Room already initiated, skipping history fetch.");
        return;
    }
    try {
        const response = await fetch(`${workerUrl}/history/${roomId}?id_participant=${participant.id_participant}`);
        if (response.ok) {
            const data = await response.json();
            const history = data.messages.map(m => ({
                id: m.id,
                type_sender: m.is_me ? 'user' : 'agent',
                msg: m.content,
                created_at: m.created_at,
                files: m.files || [],
                metadata: m.metadata || {}
            }));

            
            LiveChatSession.setMessages(roomId, history);
            scrollToBottom();
        }
    } catch (e) {
        console.error("Error fetching history:", e);
    }
}



async function scrollToBottom() {
    await tick();
    if (chat_container) {
        // chat_container is the custom element, accessing its last child or scroll property
          // chat_container.scrollTop = chat_container.scrollHeight;
          // ChatTotal uses: chat_container.scrollTop = chat_container.scrollHeight (if it was a div)
          // But chat-container is a custom element. looking at ChatTotal source:
          // chat_container.style.marginTop...
          // It seems ChatTotal styles `chat-container` as `overflow-y: auto`.
          chat_container.scrollTop = chat_container.scrollHeight;
    }
}

function chatHoursParser(str){
      if(!str) return "";
      return new FormatDate(str).hour; 
}

function getDateLabel(dateStr) {
    if (!dateStr) return "";
    const date = new FormatDate(dateStr);
    const now = new Date();
    const messageDate = new Date(dateStr);
    
    // Normalize to compare only dates
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
    const yesterday = new Date(today);
    yesterday.setDate(yesterday.getDate() - 1);
    const msgDay = new Date(messageDate.getFullYear(), messageDate.getMonth(), messageDate.getDate());

    if (msgDay.getTime() === today.getTime()) return "Hoy";
    if (msgDay.getTime() === yesterday.getTime()) return "Ayer";
    
    // For older, use dd-mm-yyyy or full_day_month_year
    // User requested: 01-04-2026 format (Day-Month-Year)
    const day = String(messageDate.getDate()).padStart(2, '0');
    const month = String(messageDate.getMonth() + 1).padStart(2, '0');
    const year = messageDate.getFullYear();
    return `${day}-${month}-${year}`;
}


// Auto-fill from URL if present
onMount(() => {
    const params = new URLSearchParams(window.location.search);
    const urlParticipantId = params.get('id_participant');
    const urlRoomId = params.get('room_id');

    if (urlRoomId) roomId = urlRoomId;

    // Check localStorage first
    const storedParticipant = localStorage.getItem('minichat_participant');

    if(registered_participant){
        if (!roomId) roomId = participant.id_participant;
        step = 'chat';
        participant = { ...participant, ...getDeviceInfo() };
        LiveChatSession.setParticipant(roomId, 'sender', participant); // Sync sender
        getHistory();
        connect();
    } else if (storedParticipant) {
        try {
            const parsed = JSON.parse(storedParticipant);
            // Ensure it has an ID
            if (parsed.id_participant) {
                participant = parsed;
                if (!roomId) roomId = participant.id_participant;
                // Auto-join
                step = 'chat';
                participant = { ...participant, ...getDeviceInfo() };
                LiveChatSession.setParticipant(roomId, 'sender', participant); // Sync sender
                getHistory();
                connect();
            }
        } catch (e) {
            console.error("Error parsing stored participant", e);
        }
    }

    if (urlParticipantId) {
          participant.id_participant = urlParticipantId;
    }

    // search by the last agent
    

    if(user_role === 'agent'){
        // Fetch receiver info if we are an agent
        // get the id of the participant
        const participantId = participant.id_participant;

        fetch(`${workerUrl}/participant/${participantId}`)
            .then(res => res.json())
            .then(data => {
                if (!data.error) {
                    LiveChatSession.setParticipant(roomId, 'receiver', data);
                }
        })
        .catch(err => console.error('Error fetching participant info:', err));
    }
});

onDestroy(() => {
    if (socket) {
        socket.close();
        socket = null;
    }
});

let uniqueDays = [];
$: {
    const days = messages.map(m => {
        const d = new Date(m.created_at);
        return new Date(d.getFullYear(), d.getMonth(), d.getDate()).getTime();
    });
    uniqueDays = [...new Set(days)];
}
</script>

<chat-component class="py-0 is-flex is-flex-direction-column is-relative {containerClasses}" style="{containerStyles}">
    <section class="is-flex is-flex-direction-column is-full-width">
        
        <!-- Header -->
        <header-chat class="is-flex is-flex-direction-column is-full-width pb-0 padding-20">
            <div class="is-flex is-align-items-center is-justify-content-space-between is-full-width">
                 <div class="is-flex is-align-items-center is-justify-content-center is-gap-10">
                     <div class="avatar-circle">
                         <img src="{ (user_role === 'agent' && receiverParticipant?.profile_picture) ? receiverParticipant.profile_picture : 'https://i.pravatar.cc/150?img=26' }" alt="User" />
                     </div>
                     <div class="is-flex is-flex-direction-column">
                         <h4 class="font-size-16 fw600">
                           { (user_role === 'agent' && receiverParticipant) ? receiverParticipant.name : 'Soporte 3Pod' }
                         </h4>
                         {#if step === 'chat'}
                            <span class="is-flex is-align-items-center is-gap-5">
                                <span class="font-size-10 {status === 'connected' ? 'has-text-success' : 'has-text-danger'}">
                                    ⬤
                                </span>
                                <span class="font-size-12 has-text-mid-grey">
                                    {status === 'connected' ? 'En línea' : status}
                                </span>
                            </span>
                         {/if}
                     </div>
                 </div>
            </div>
        </header-chat>

        <!-- Body -->
        <chat-container class="{messages.length === 0 ? 'is-justify-content-center' : ''} py-0 is-flex custom-scrollbar-gray is-full-width step-{step}" style="flex-direction: column; overflow-y: {step === 'chat' ? 'auto' : ''}; flex: 1;" bind:this={chat_container}>
            
            {#if step === 'register'}
                <div class="join-container is-flex is-flex-direction-column is-align-items-center is-justify-content-center is-full-height is-full-width is-gap-20">
                    <div class="text-center">
                        <h3 class="font-size-20 fw600 mb-10">Bienvenido!</h3>
                        <p class="font-size-14 fw400">Por favor, ingrese sus datos para comenzar a chatear.</p>
                    </div>
                    
                    <form class="is-flex is-flex-direction-column is-gap-15 is-full-width" style="max-width: 300px;" on:submit|preventDefault={joinChat}>
                        <div class="field">
                            <label class="font-size-12 fw500" for="name">Nombre</label>
                            <input class="input is-full-width" type="text" id="name" bind:value={participant.name} placeholder="Tu nombre" required />
                        </div>
                        <div class="field">
                            <label class="font-size-12 fw500" for="email">Correo</label>
                            <input class="input is-full-width" type="email" id="email" bind:value={participant.email} placeholder="tu@correo.com" required />
                        </div>
                         <div class="field">
                            <label class="font-size-12 fw500" for="phone">Teléfono (Opcional)</label>
                            <InternationalNumberInput 
                                bind:selected_dial={participant.phone_dial}
                                bind:selected_code={participant.phone_code_country}
                                bind:selected_value={participant.phone}
                                ClassInputs="formElements int-phone"
                                has_static_dial={false}
                                Results={(r)=>{ validations_phones.phone = r; }}
                                names={{
                                    dial: "phone_dial",
                                    phone: "phone",
                                    country: "phone_code_country"
                                }}
                            />  
                        </div>
                        <button class="button is-primary is-full-width mt-2" type="submit">
                            Comenzar Chat
                        </button>
                    </form>
                </div>
            {:else}
                <!-- Chat Messages -->
                 {#if messages.length > 0}
                    <container-messages class="is-full-width is-flex is-flex-direction-column is-gap-5">
                        {#each messages as message, key}
                            <!-- Date Separator -->
                            {#if key === 0 || (new Date(messages[key-1].created_at).toDateString() !== new Date(message.created_at).toDateString())}
                                {#if !(uniqueDays.length <= 1 && getDateLabel(message.created_at) === 'Hoy')}
                                    <div class="message-separator is-flex is-justify-content-center my-4 has-text-dark fw300">
                                        <span class="px-3 py-1 font-size-10">
                                            { getDateLabel(message.created_at) }
                                        </span>
                                    </div>
                                {/if}
                            {/if}

                             {#if message.type_sender === 'user'}
                                <!-- Outgoing (User) -->
                                <outgoing-message class="is-flex message-item is-flex-direction-column is-align-items-flex-end is-gap-17 is-full-width">
                                    <span>
                                        <p class="font-size-14" style="word-break: break-word;">
                                            <MessagesParser message={message.msg} />
                                        </p>
                                        <p class="is-full-width text-right font-size-10 opacity-70 mt-1 chat-time">
                                            {chatHoursParser(message.created_at)}
                                        </p>
                                    </span>
                                </outgoing-message>
                             {:else}
                                <!-- Incoming (Admin/Agent) -->
                                <incoming-message class="is-flex message-item is-flex-direction-column is-align-items-flex-start is-gap-17 is-full-width">
                                     <span>
                                        <p class="font-size-14" style="word-break: break-word;">
                                            <MessagesParser message={message.msg} />
                                        </p>
                                        <p class="is-full-width text-left font-size-10 opacity-70 mt-1 chat-time">
                                            {chatHoursParser(message.created_at)}
                                        </p>
                                    </span>
                                </incoming-message>
                             {/if}
                        {/each}
                    </container-messages>
                 {:else}
                    <div class="is-flex is-align-items-center is-justify-content-center">
                        <p class="has-text-mid-grey">No hay mensajes todavía.</p>
                    </div>
                 {/if}
            {/if}

        </chat-container>

        <!-- Footer -->
        {#if step === 'chat'}
            <footer-chat class="padding-20 pt-0">
                <form class="is-flex is-align-items-center is-justify-content-space-between is-full-width is-gap-13" on:submit|preventDefault={sendMessage}>
                    <!-- File input (mock for now) -->
                     <span class="is-relative">
                        <i class="icon-tripod-link font-size-24 has-text-mid-grey is-clickable pointer"></i>
                     </span>

                    <span class="chat-writer is-full-width">
                         <!-- Using simple input for now instead of contenteditable div for simplicity with binding -->
                         <input 
                            type="text" 
                            class="custom-textarea is-full-width"  
                            placeholder="Escribe un mensaje..." 
                            bind:value={newMessage}
                            style="border: none; padding: 10px; border-radius: 8px; background: #f5f5f5;" 
                         />
                    </span>
                    <button type="submit" class="is-borderless" style="background-color: transparent;">
                        <i class="icon-tripod-send-message has-text-mid-grey is-clickable is-flex is-align-items-center is-justify-content-center"></i>
                    </button>
                </form>
            </footer-chat>
        {/if}

    </section>
</chat-component>

<style>
/* Scoped styles replicating ChatTotal basics + Join Form */
chat-component {
    display: flex;
    flex-direction: column;
    background: white;
    height: 100%;
    overflow: hidden; 
}

section {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

header-chat {
    width: 100%;
}

footer-chat {
    width: 100%;
    padding-bottom: 10px;
}

chat-container {
    gap: 10px;
    flex: 1;
}

/* Bubbles similar to ChatTotal */
outgoing-message {
    align-self: flex-end;
    margin-left: auto;
}
outgoing-message span {
    background: var(--color-primary, #6366f1);
    color: white;
    padding: 10px 15px;
    border-radius: 12px 12px 0 12px;
    max-width: 80%;
}
outgoing-message p {
    color: white;
    margin: 0;
}


incoming-message {
    align-self: flex-start;
}
incoming-message span {
    background: var(--color-lightGrey, #f3f4f6);
    color: var(--color-info, #1f2937);
    padding: 10px 15px;
    border-radius: 12px 12px 12px 0;
    max-width: 80%;
}
incoming-message p {
     color: var(--color-info, #1f2937);
     margin: 0;
}
container-messages{
    width: 95%;
    min-width: 250px;
    margin: auto;
}

/** last item message */
.message-item:last-child {
    margin-bottom: 20px;
}
.field{
  width: 100%;
  margin-bottom: 0;
}

/* Join Form Styles */
.join-container {
    padding: 20px;
}
.avatar-circle {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    background: #eee;
}
.avatar-circle img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.chat-time{
    opacity: 0.7;
}
.message-separator{
    opacity: 0.7;
    color: #b1b3b6;
}
</style>